.section_1 {
    background-color: #ee5454;
    color: #fff;

    &__logo {
        margin-bottom: 70px;
    }

    &__form {
        margin-bottom: 20px;
    }

    &__form-input {
        font-family: Ubuntu, sans-serif;
        font-style: italic;
        max-width: 600px;

        &_type_name {
            width: 360px;
        }

        &_type_brand {
            width: 160px;
            max-width: 350px;
        }

        &_type_email,
        &_type_phone {
            width: 350px;
        }
    }

    &__form-header {
        margin-bottom: 40px;
    }

    &__form-contacts {
        font-style: italic;
        font-family: Ubuntu, sans-serif;
        display: inline-block;
        margin-bottom: 50px;
    }

    &__form-contacts-item {
        color: #eeb0ae;
        text-align: left;
        font-size: 20px;
        margin-bottom: 5px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__error-message {
        color: #fff8a3;
        font-size: 20px;
        line-height: 60px;
        visibility: hidden;
    }

    &__success-message {
        margin-bottom: 180px;
        display: none;
    }

    &__stat-item {
        color: #ffcece;
        white-space: nowrap;
    }

    &__stat-item-title {
        font-size: 33px;
        font-weight: bold;
    }

    &__stat-item-comment {
        font-size: 15px;
        line-height: 1.4;
    }
}
