.button {
    cursor: pointer;
    border: none;
    border-radius: 6px;
    font-size: 17px;
    line-height: 47px;
    outline: none;
    padding: 0 17px;
    text-transform: uppercase;
    white-space: nowrap;
    user-select: none;
    min-width: 210px;
}

.button.button_white {
    background-color: #fff;
    color: #ee5454;

    &:hover {
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
        color: #000000;
    }

    &:active {
        color: rgba(0, 0, 0, 0.7);
    }
}

.button.button_white.disabled {
    background-color: rgba(255, 255, 255, 0.3);
    color: #ee5454;
    cursor: auto;
    pointer-events: none;
    box-shadow: none !important;
}

.button.button_red {
    background-color: #ee5454;
    color: #fff;

    &:hover {
        background-color: #ed3535;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
    }

    &:active {
        background-color: #ed3535;
        color: rgba(255, 255, 255, 0.7);
    }
}

.button.button_blue {
    background-color: #36a6fd;
    color: #fff;

    &:hover {
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
        background-color: #007fe2;
    }

    &:active {
        color: rgba(255, 255, 255, 0.7);
    }
}

.button.button_blue.disabled {
    background-color: rgba(255, 255, 255, 0.2);
    color: rgba(255, 255, 255, 0.45);
    cursor: auto;
    pointer-events: none;
    box-shadow: none !important;
}
